.mes-projet {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 1rem;

  h2 {
    color: white;
    font-size: 2rem;

    span {
      color: rgba(124, 214, 101, 0.6); 
    }
  }

  .projet-img {
    display: flex;
    gap: 1.5rem;

    .image-container {
      position: relative;
      width: 300px;
      height: 300px;
      overflow: hidden;
      border-radius: 10px;
      cursor: pointer;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: opacity 0.3s ease-in-out;
      }

      /* Overlay rouge semi-transparent */
      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(124, 214, 101, 0.6); /* Rouge avec transparence */
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
        z-index: 1;
      }

      /* Titre bien visible */
      .image-title {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 1.8rem;
        font-weight: bold;
        text-align: center;
        opacity: 0;
        z-index: 2; /* Assure qu'il est au-dessus */
        transition: opacity 0.3s ease-in-out;
      }

      &:hover {
        img {
          opacity: 0.6; /* Assombrit un peu l'image */
          transform: scale(1.05);
        }

        .overlay {
          opacity: 1; /* Affiche l’overlay rouge */
        }

        .image-title {
          opacity: 1; /* Affiche le titre */
        }
      }
    }
  
}
.trouver-projet{
  display: flex;
align-items: end;
justify-content: end;
margin-right: 26rem;
  width: 100%; /* Prend toute la largeur */
  margin-top: 2rem; 


  .btn-trouver-projet{
