    border: none;
    cursor: pointer;
    .fa-solid {
      display: inline-block;
      font-size: 1rem; /* Ajuste la taille */
    }
  }
  
}
}

}






}

@keyframes wave {
  0% { transform: rotate(0deg); }
 100% { transform: rotate(20deg); }
}
@keyframes slideAndFade {
  0% { 
    transform: translateX(0%); /* Hors écran à gauche */
    opacity: 0; /* Caché */
  }
  20% { opacity: 1; } /* Il devient visible */
  50% { 
    transform: translateX(50%); /* Il se déplace vers la droite */
    opacity: 1;
  }
  80% { opacity: 1; } /* Visible avant de disparaître */
  100% { 
    transform: translateX(150%); /* Hors écran à droite */
    opacity: 0; /* Il disparaît */
  }
}

